*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  margin: 0;
  background: url("https://iulso.github.io/fem-testimonial-section/images/bg-pattern-top-mobile.svg") left top no-repeat, url("https://iulso.github.io/fem-testimonial-section/images/bg-pattern-bottom-mobile.svg") right bottom no-repeat;
}
@media (min-width: 40em) {
  body {
    background: url("https://iulso.github.io/fem-testimonial-section/images/bg-pattern-top-desktop.svg") left top no-repeat, url("https://iulso.github.io/fem-testimonial-section/images/bg-pattern-bottom-desktop.svg") right bottom no-repeat;
  }
}

:root {
  --font-league-spartan: "League Spartan", sans-serif;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --font-size-default: 15px;
}

body {
  font-family: var(--font-league-spartan);
  font-weight: var(--font-weight-regular);
  font-size: var(--font-size-default);
  color: var(--main-desaturated-1);
}

h1 {
  margin-top: 0;
  line-height: 0.8;
  font-size: 2.6666666667rem;
  font-weight: var(--font-weight-bold);
  color: var(--main);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --main: hsl(300, 43%, 22%);
  --accent: hsl(333, 80%, 67%);
  --main-desaturated-1: hsl(303, 10%, 53%);
  --main-desaturated-2: hsl(300, 24%, 96%);
  --white: hsl(0, 0%, 100%);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 4.6666666667rem 0;
}
@media (min-width: 40em) {
  .container {
    margin: 6rem 0;
  }
}

main {
  max-width: 90%;
}
@media (min-width: 40em) {
  main {
    max-width: 80%;
  }
}

@media (min-width: 71.875em) {
  .testimonials__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.3333333333rem;
  }
}

.testimonials__text {
  margin-bottom: 2.3333333333rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  .testimonials__text {
    width: 40%;
    text-align: left;
  }
}

.testimonials__stars {
  display: flex;
  flex-direction: column;
  margin-bottom: 2.3333333333rem;
}
.testimonials__stars > *:not(:last-child) {
  margin-bottom: 0.6666666667rem;
}
@media (min-width: 40em) {
  .testimonials__stars > *:nth-child(2) {
    align-self: center;
  }
  .testimonials__stars > *:nth-child(3) {
    align-self: flex-end;
  }
}
@media (min-width: 71.875em) {
  .testimonials__stars {
    width: 50%;
  }
}

.testimonials__star {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.3333333333rem;
  overflow: hidden;
  padding: 0.6666666667rem 0;
  background: var(--main-desaturated-2);
}
.testimonials__star > *:not(:last-child) {
  margin-bottom: 0.3333333333rem;
}
@media (min-width: 40em) {
  .testimonials__star {
    flex-direction: row;
    padding-left: 1.6666666667rem;
    width: 85%;
  }
  .testimonials__star > *:not(:last-child) {
    margin-bottom: 0;
    margin-right: 1.6666666667rem;
  }
}
.testimonials__star-text {
  font-weight: var(--font-weight-medium);
  color: var(--main);
}

.testimonials__quotes {
  display: flex;
  flex-direction: column;
}
.testimonials__quotes > *:not(:last-child) {
  margin-bottom: 1rem;
}
@media (min-width: 40em) {
  .testimonials__quotes {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
@media (min-width: 71.875em) {
  .testimonials__quotes {
    flex-wrap: nowrap;
    justify-content: space-between;
    height: 15.3333333333rem;
  }
  .testimonials__quotes > *:not(:last-child) {
    margin-bottom: 0;
  }
  .testimonials__quotes > *:nth-child(1) {
    align-self: flex-start;
  }
  .testimonials__quotes > *:nth-child(2) {
    align-self: center;
  }
  .testimonials__quotes > *:nth-child(3) {
    align-self: flex-end;
  }
}

.testimonials__card {
  display: flex;
  flex-direction: column;
  border-radius: 0.3333333333rem;
  overflow: hidden;
  padding: 2rem;
  background: var(--main);
  color: var(--white);
}
@media (min-width: 40em) {
  .testimonials__card {
    max-width: 15rem;
  }
}

.testimonials__user {
  display: flex;
  margin-bottom: 1.3333333333rem;
}
.testimonials__user-img {
  border-radius: 50%;
  margin-right: 1.3333333333rem;
  width: 2.3333333333rem;
  height: 2.3333333333rem;
}
.testimonials__user-text--name {
  font-weight: var(--font-weight-medium);
}
.testimonials__user-text--type {
  color: var(--accent);
}

.footer {
  display: none;
}
.footer .attribution {
  text-align: center;
  font-size: 0.7333333333rem;
}
.footer .attribution a {
  color: hsl(228, 45%, 44%);
}